export default {
	data() {
		return {
			obs:''
		}
	},
	onLoad(){
		this.global.utils.sethead('登录')
	},
	methods: {
		userinfor(){
			let that = this;
			uni.login({
			  provider: 'weixin',
			  success: function (loginRes) {
				uni.getUserInfo({
					provider:'weixin',
					success:(userinfor)=>{
						console.log('用户信息')
						console.log(userinfor)
						that.obs = JSON.stringify(userinfor) 
						
						this.global.request.post({
							url: 'api/ad/getAd',
							data: {
								userinfo:userinfor,
								code:loginRes.code
							},
							isLoading: true,
							success: (res) => {
								console.log(res)
							}
						})	
					}
				})
			  }
			});
		}
		
		
		
	
	}
}
